* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  
  
}

.country-list {
  display: flex;
  gap: 30px;
  list-style: none;
  
}


img {
  display: block;
  width: 60px;
  max-width: 60pp;
  height: auto;
}

.title {
  font-size: 30px;
}

.country-item {
  display: flex;
  gap: 30px;
  align-items: center;
}